import React, { PureComponent } from 'react';
import Test from '../components/Loader';
import EditIcon from '@material-ui/icons/Edit';
import Visibility from '@material-ui/icons/Visibility';
import { withStyles } from '@material-ui/core/styles';
import EnhancedMenu from '../components/EnhancedMenu';

const style = {
    icon: {
        marginRight: 10
    }
}

class _404 extends PureComponent {
    render() {
        const { classes } = this.props;
        return (
            <React.Fragment>
                {/* <h1>404 Not Found!</h1> */}
                {/* <Test></Test> */}
                <EnhancedMenu
                    {...this.props}
                    options={[
                        {
                            jsx: {
                                icon: <Visibility className={classes.icon} />,
                                content: "_404",
                                tooltip: "this is 404"
                            },
                            onClick: e => console.log("create_404", e)
                        },
                        { jsx: <React.Fragment><Visibility className={classes.icon} />_404</React.Fragment>, onClick: e => console.log("_404", e) },
                        { jsx: <React.Fragment><EditIcon className={classes.icon} />Another_404</React.Fragment>, onClick: e => console.log("Another_404", e) },
                    ]}
                />
            </React.Fragment>
        );
    }
}

export default withStyles(style)(_404);



